.title {
  color: blue;
  text-align: center;
}

.list {
  display: flex;
  flex-wrap: wrap;
}

.item {
  padding: 10px 30px;
  list-style: none;
  margin: 0;
}
.btn {
  padding: 10px;
  border: 2px solid rgb(70, 16, 70);
  text-decoration: none;
  font-weight: 700;
  color: rgb(70, 16, 70);
  border-radius: 10px;
  cursor: pointer;
}
.btn:hover {
  background-color: rgb(157, 211, 218);
}
